.NavBar{
    height: 45px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: red;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0, 20px;
    box-sizing: border-box;
    z-index: 90;
    
}

.NavBar nav {
    height: 100%;
}

